.detail-content {
  background: #f5f6f7;
  width: 100%;
  height: 100%;
  padding-top: 45px;
  overflow: scroll;
  position: relative; }
  .detail-content .banner {
    height: 150px;
    position: relative; }
    .detail-content .banner img {
      width: 100%;
      height: 100%; }
    .detail-content .banner .more-pic {
      position: absolute;
      width: 80px;
      height: 30px;
      background: rgba(0, 0, 0, 0.8);
      color: #fff;
      text-align: center;
      line-height: 30px;
      bottom: 15px;
      right: 0; }
  .detail-content .nav-detail {
    width: 100%;
    height: 50px;
    padding: 10px;
    display: -webkit-flex;
    background: #fff; }
    .detail-content .nav-detail div {
      flex: 1;
      text-align: center;
      border: 1px solid #24aef2;
      line-height: 28px;
      color: #24aef2; }
      .detail-content .nav-detail div:first-child {
        border-radius: 5px 0 0 5px; }
      .detail-content .nav-detail div:last-child {
        border-radius: 0 5px 5px 0; }
      .detail-content .nav-detail div.detail-active {
        color: #fff;
        background: #24aef2; }
  .detail-content .nav-box {
    background: #fff;
    width: 100%; }
    .detail-content .nav-box .base-news {
      width: 100%;
      float: left; }
      .detail-content .nav-box .base-news h3 {
        height: 40px;
        line-height: 40px;
        padding-left: 15px;
        background: #fff;
        border-bottom: 1px solid #eee; }
      .detail-content .nav-box .base-news p {
        height: 40px;
        line-height: 40px;
        margin-left: 15px;
        border-bottom: 1px solid #eee;
        color: #666; }
        .detail-content .nav-box .base-news p i {
          color: #666;
          margin-right: 10px; }
        .detail-content .nav-box .base-news p:last-child {
          line-height: 50px;
          height: 50px; }
    .detail-content .nav-box .hotel-introduce {
      width: 100%;
      float: left;
      background: #fff;
      height: 170px;
      padding-left: 15px; }
  .detail-content .order-hotel {
    width: 100%;
    margin-top: 15px;
    background: #fff; }
    .detail-content .order-hotel .hotel-date {
      position: relative;
      top: 0;
      width: 100%;
      height: 38px;
      display: -webkit-flex;
      line-height: 38px;
      border-bottom: 1px solid #ccc;
      background: #fff; }
      .detail-content .order-hotel .hotel-date p {
        flex: 1;
        text-align: center; }
        .detail-content .order-hotel .hotel-date p span {
          margin-right: 7px; }
        .detail-content .order-hotel .hotel-date p.recompose {
          color: #24aef2; }
          .detail-content .order-hotel .hotel-date p.recompose span {
            padding-left: 40px; }
          .detail-content .order-hotel .hotel-date p.recompose i {
            font-size: 12px; }
    .detail-content .order-hotel .type-box {
      height: 240px;
      overflow: hidden; }
      .detail-content .order-hotel .type-box .type-list {
        display: -webkit-flex;
        height: 80px;
        line-height: 80px;
        border-bottom: 1px solid #eee; }
        .detail-content .order-hotel .type-box .type-list div:nth-child(1) {
          flex: 1;
          padding-left: 15px; }
          .detail-content .order-hotel .type-box .type-list div:nth-child(1) h3 {
            line-height: 20px;
            margin-top: 20px;
            font-size: 14px; }
          .detail-content .order-hotel .type-box .type-list div:nth-child(1) p {
            line-height: 20px;
            color: #999; }
        .detail-content .order-hotel .type-box .type-list div:nth-child(2) {
          flex: 1;
          text-align: right;
          padding-right: 20px;
          color: orange; }
        .detail-content .order-hotel .type-box .type-list div:nth-child(3) {
          width: 60px;
          height: 25px;
          text-align: center;
          line-height: 25px;
          border: 1px solid #24aef2;
          margin: 25px 10px 0 0;
          color: #24aef2; }
        .detail-content .order-hotel .type-box .type-list div.full-hotel {
          background: #eee;
          color: #ccc;
          border: 1px solid #ccc; }
  .detail-content .recommend-hotel {
    margin-top: 15px;
    background: #fff; }
    .detail-content .recommend-hotel h3 {
      line-height: 30px;
      padding-left: 10px; }
    .detail-content .recommend-hotel .hotel-list h3 {
      line-height: 20px; }
    .detail-content .recommend-hotel .list-box {
      height: 300px;
      overflow: hidden; }
    .detail-content .recommend-hotel .special-box {
      height: 300px;
      overflow: hidden; }
  .detail-content .display-more {
    width: 100%;
    text-align: center;
    height: 30px;
    line-height: 30px;
    color: #999;
    background: #fff; }

.all-img {
  position: absolute;
  width: 100%;
  height: 150px;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  left: 0;
  z-index: 33; }
  .all-img .more-banner {
    height: 100%; }
    .all-img .more-banner img {
      width: 100%;
      height: 100%; }
    .all-img .more-banner li {
      display: -webkit-flex;
      -webkit-align-items: center; }

.mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  left: 0;
  top: 0;
  z-index: 5; }
